<template>
    <tg-list v-show="IsView" :IsShowAction="false" ModuleName="Ticketing" SubModuleCode=""
        IdentityColumn="ID" :HeaderText="$t('Transform Pending')" :LegendArray="[]" :RenderRowActionMethod="() => { }"
        :widgets="[]" :listType="['List']" :ListData="TransformPendingList" :HeaderData="Headers" :HeaderButtons="listheaderbuttons"
        :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchData"
        :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
        v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
        v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
        <template v-slot:slotdata="{ data }">
            <template v-if="data.column.COLUMN_NAME == 'Action'">
                <td class="text-center single-action tourguide_aa">
                    <a  :typetitle="$t('View')" typefor="View"
                            v-bind:dataid="data.row.ID" :title="$t('View')">
                            <em class="fa fa-eye  text-info action-icon"></em>
                        </a>   
                </td>
            </template>
        </template>
    </tg-list>
</template>
<script>
import Schema from "@/views/TransformPending/TransformPendingSchema";
import DataServices from "@/services/DataService";
export default {

    data() {
        return {
            IsView:true,    
            Id: null,
            CompanyId: '',
            UserId: '',
            SortBy: '',
            SortExp: '',
            PageSize: 10,
            PageNumber: 1,
            CurrentPage: 1,
            GroupStart: 1,
            GroupEnd: 3,
            TransformPendingList: [],
            NoRecord: false,
            TotalRecords: 0,
            NoRecord: true,
            ticketNumber: '',
            SearchCondition: '',
            showViewTicketTextModal: false,
            TicketID: '',
            TicketTextDetail: "",
            IsSearchTrueOrFalse: false,
            Headers: [{
                "COLUMN_NAME": "TICKETNUMBER",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "TICKET NUMBER",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "STREET",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "STREET",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "COUNTY",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "COUNTY ",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "CITY",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "status",
                "DISPLAY_ORDER": 4
            },
            {
                "COLUMN_NAME": "TYPEOFWORK",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "TYPEOFWORK",
                "DISPLAY_ORDER": 4
            },
            {
                "COLUMN_NAME": "EMAIL_ID",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "EMAIL",
                "DISPLAY_ORDER": 4
            },
            {
                "COLUMN_NAME": "Action",
                "DATA_TYPE": "",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Action",
                "DISPLAY_ORDER": 6,
            },
            
            
            
            ],
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
        }
    },
    created: async function () {
        debugger
        this.UserId = this.GetUserInfo.ID
        this.CompanyId = this.GetUserInfo.CompanyId;
        await this.FetchData();
    },

    methods: {
        paramsToObject(entries) {
            const result = {}
            for (const [key, value] of entries) { // each 'entry' is a [key, value] tupple
                result[key] = value;
            }
            return result;
        },
        leftCommonSearch: function (value) {
            debugger;
            const searchParams = new URLSearchParams(value.searchConditionJson);
            const entries = searchParams.entries();
            const params = this.paramsToObject(entries);
            if (params.hasOwnProperty('search')) {
                this.search = params['search'];
            }
            else{
                this.search=''; 
            }
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        FetchData: async function () {
            var vueObj = this;
            var url = `sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&lang=${'en'}`;
         
            if (!this.isBlank(this.search)) {
                url += `&search=${this.search}`;
            }
            await DataServices.GetTransformPendingList(url).then(function (response) {
                debugger
                if (response.data != "" && response.data != null) {
                    vueObj.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "Action") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                    });
                    if (response.data.result.DATA.length > 0) {
                        debugger
                        vueObj.TransformPendingList = response.data.result.DATA;

                        vueObj.TotalRecords = response.data.result.DATA[0].TOTALRECORDS;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        vueObj.noRecord = false;
                        vueObj.IsLoad = true;
                    } else {
                        vueObj.noRecord = true;
                        vueObj.TransformPendingList = [];
                        vueObj.TotalRecords = 0
                    }
                }
                else {
                    vueObj.noRecord = true;
                    vueObj.TransformPendingList = [];
                    vueObj.Headers = [];
                    vueObj.TotalRecords = 0
                }             
                setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
            })
        },
        commonHeaderButtonClick: function (event) {

            switch (event.callbackfunction) {
                case "openaddTicketGroup":
                    this.openaddTicketGroup();
                    break;
              
            }
        },
        sortdata: function (sortBy) {

            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchData();
        },
       
       
       
    }
}
</script>